<template lang="pug">
  .aside-filter
    h2.aside-filter__title 
    ul.aside-filter__list
      li.aside-filter__item(v-for="item in list" :key="item.component" :class="{active: item.component === value}" @click="changeComponent(item)") {{item.text}}
</template>

<script>
export default {
  name: 'SettingsSidebar',
  props: {
    value: String
  },
  data: () => ({
    list: [
      { component: 'settings-main', text: '' },
      { component: 'settings-push', text: ' ' },
      { component: 'settings-security', text: '' },
      { component: 'settings-delete', text: ' ' }
    ]
  }),
  methods: {
    changeComponent(item) {
      this.$emit('change-component', item)
    }
  }
}
</script>



